.big_body {
  background: rgb(1, 1, 1);
  background: -moz-linear-gradient(
    0deg,
    rgba(1, 1, 1, 1) 0%,
    rgba(17, 24, 34, 1) 100%
  );
  background: -webkit-linear-gradient(
    0deg,
    rgba(1, 1, 1, 1) 0%,
    rgba(17, 24, 34, 1) 100%
  );
  background: linear-gradient(
    0deg,
    rgba(1, 1, 1, 1) 0%,
    rgba(17, 24, 34, 1) 100%
  );
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#010101",endColorstr="#111822",GradientType=1);
  width: 100%;
  min-height: 700px;
  height: 100vh;
  position: relative;

  .container {
    position: absolute;
    top: 10%;
    left: 10%;
    right: 10%;
    bottom: 10%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    // background: url("../images/mercury.jpg"), #151729;
    background-size: 100%;
    background-position: top;
    box-shadow: 0 15px 30px rgba(0, 0, 0, 0.5);
    
    min-height: 500px;

    .bg_img {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: #151729;
      width: 100%;
      min-height: 500px;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: top;

      }
    }

    .content {
      max-width: 600px;
      text-align: center;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;

      h2 {
        font-size: 10vw;
        color: #fff;
        line-height: 1em;
        z-index: 5;
      }

      h4 {
        position: relative;
        font-size: 2vw;
        margin-bottom: 20px;
        color: #fff;
        // background: #fff;
        font-weight: 300;
        padding: 10px 20px;
        display: inline-block;
        text-transform: uppercase;
        z-index: 5;
      }

      p {
        color: #fff;
        font-size: 1.2em;
        z-index: 5;
      }

      a {
        position: relative;
        display: inline-block;
        padding: 10px 25px;
        background: rgb(212, 96, 69);
        background: -moz-linear-gradient(
          0deg,
          rgba(212, 96, 69, 1) 0%,
          rgba(237, 174, 112, 1) 100%
        );
        background: -webkit-linear-gradient(
          0deg,
          rgba(212, 96, 69, 1) 0%,
          rgba(237, 174, 112, 1) 100%
        );
        background: linear-gradient(
          0deg,
          rgba(212, 96, 69, 1) 0%,
          rgba(237, 174, 112, 1) 100%
        );
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#d46045",endColorstr="#edae70",GradientType=1);
        color: #fff;
        text-decoration: none;
        border-radius: 25px;
        margin-top: 25px;
        z-index: 5;

        &:hover {
          filter: brightness(0.4);
        }
      }
    }
  }

  .prev_planet, .next_planet {
    position: absolute;
    bottom: 10%;
    display: flex;
    align-items: center;
    color: #fff;
    

    .image-box {
      width: 50px;
      height: 50px;
      margin: 0 15px;

      img {
        width: 100%;
        height: 100%;
      }
    }

  }
  .prev_planet {
    left: 15%;
    flex-direction: row;
  }
  .next_planet {
    right: 15%;
    flex-direction: row-reverse;
  }

  .planet_info, .info_asteroid {
    position: absolute;
    color: #fff;
    display: flex;
    flex-direction: column;
    font-size: small;

    h3 {
      margin: 10px 0;
      font-weight: 600;
    }
    p { 
      font-weight: 100;
      margin-bottom: 20px;
    }
  }
  .planet_info {
    right: 15%;
    text-align: right;
    bottom: 30%;
  }
  
  .info_asteroid {
    left: 15%;
    text-align: left;
    bottom: 40%;
    max-width: 100px;


    h3 {
      margin: 20px 0;
    }

    .image-box {
      width: 50px;
      height: 50px;

      img {
        width: 100%;
        height: 100%;
      }
    }
  }
}
