.card {
  position: relative;
  width: 100%;
  height: 75vh;
  margin: 0 auto;
  background: #ccc;
  overflow: hidden;
  border-radius: 10px;
  transition: 0.5s;

  &:hover {
    transform: translateY(-20px);
    box-shadow: 0 20px 20px rgba(0, 0, 0, 0.2);
  }

  .img-box {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: 0.5s;

    img {
      height: 100%;
      width: 100%;
      object-fit: cover;
      -webkit-box-reflect: below 1px
        linear-gradient(transparent, transparent, #0006);
    }
  }

  &:hover .img-box {
    opacity: 0.5;
    cursor: pointer;
  }

  &:hover .content {
    bottom: 0;
    box-shadow: 0 20px 20px rgba(0, 0, 0, 0.2);
  }

  .content {
    position: absolute;
    width: 100%;
    height: 60%;
    bottom: -100%;
    padding: 20px;
    box-sizing: border-box;
    text-align: center;
    transition: 0.5s;
    padding-top: 60px;

    

    h2 {
      margin: 0 0 10px;
      font-size: 20px;
      color: #fff;
      padding: 0;

      span {
        color: #ffeb3b;
        font-size: 16px;
      }
    }
  }

